import AbstractView from '../view.js';
import * as data from '../data.js';

export default class ResultFailView extends AbstractView {

  constructor(stats) {
    super();

    this._stats = stats;
  }

  get template() {
    return `<section class="main main--result">
        <section class="logo" title="${data.gameInfo.gameName}"><h1>${data.gameInfo.gameName}</h1></section>
        <h2 class="title">  !</h2>
        <div class="main-stat">&nbsp;${this.getTimeString()}<br>&nbsp; ${this._stats.answers}&nbsp;</div>
        <span class="main-comparison">&nbsp;  &nbsp;${this._stats.percentHighscore}&nbsp;</span>
        <span role="button" tabindex="0" class="main-replay">  </span>
      </section>`;
  }

  onRestartClick() {

  }

  bind() {
    const screenDom = this.element;

    const button = screenDom.querySelector(`.main-replay`);
    button.onclick = () => {
      this.onRestartClick();
    };

    return screenDom;
  }

  getTimeString() {
    const time = this._stats.time;

    const mins = Math.floor(time / 60);
    const secs = time - (mins * 60);

    if (mins) {
      return `${mins}  ${secs} `;
    } else {
      return `${secs} `;
    }
  }
}
